<template>
  <div class="main">
    <Header @selectHandle="selectHandle" v-show="!store.state.isPreview" />
    <Edit />
    <List :height="wraperHeight" :visible="listVisible" />
    <Attr :height="wraperHeight" />
    <CustomMenu />
    <div
      class="pre-btn canclePreview"
      v-show="store.state.isPreview"
      @click="editHandle"
    >
      编辑
    </div>
    <div
      class="pre-btn createImg"
      v-show="store.state.isPreview"
      @click="createImgHandle"
    >
      生成图片
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs } from "vue";
import Header from "../header/index.vue";
import Edit from "../edit/index.vue";
import List from "../list/index.vue";
import Attr from "../attr/index.vue";
import CustomMenu from "@/components/customMenu/index.vue";
import useSize from "@/hooks/useSize";
import { useStore } from "vuex";
import domtoimage from "dom-to-image";
import saveAs from "save-as";

export default defineComponent({
  name: "Main",
  components: {
    Header,
    Edit,
    List,
    CustomMenu,
    Attr,
  },
  setup() {
    const { wraperHeight } = useSize();
    const store = useStore();

    const state = reactive<{
      listVisible: boolean;
    }>({
      listVisible: false,
    });

    const selectHandle = (listVisible: boolean): void => {
      state.listVisible = listVisible;
    };

    const editHandle = () => {
      store.commit("set_isPreview", false);
    };

    const createImgHandle = (): void => {
      const el: any = document.getElementById("edit");
      domtoimage.toBlob(el).then(function (blob: any): void {
        saveAs(blob, "img.png");
      });
    };

    return {
      store,
      editHandle,
      ...toRefs(state),
      selectHandle,
      wraperHeight,
      createImgHandle,
    };
  },
});
</script>
<style lang="scss" scoped>
.main {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.pre-btn {
  position: fixed;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  background: rgba(64, 158, 255, 0.9);
  z-index: 9;
  color: #fff;
  border-radius: 50%;
  font-size: 12px;
  cursor: pointer;
  right: 10px;
  font-size: 12px;
}
.canclePreview {
  top: 10px;
}
.createImg {
  top: 80px;
}
</style>
